




























































.field {
  .field-display {
    span {
      margin-right: 6px;
  }
    svg.svg-inline--fa {
      font-size: 0.875em;
      vertical-align: 0;
    }
  }
  tr {
    .field-display span {
      margin-right: 4px;
    }
    svg.svg-inline--fa {
      font-size: 10px;
      vertical-align: 0.2em;
    }
  }
}
.uk-fieldset {
  margin: 36px 0 20px;
  h4 {
    margin: 0;
    text-transform: capitalize;
  }
  .titlesep {
    margin: 0 0 12px;
  }
}
